@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@300&display=swap);
*{
    padding:0;
    margin: 0;
    box-sizing :border-box;
    /* font-family: 'Mulish', sans-serif; */
}

.navbar{
    background-color:white !important;
}
.navbar a{
    font-size:1.3rem;
    text-transform:capitalize;
}
.navbar-brand{
    font-size: 1.9rem !important;
    color:#3498db !important;
    font-weight: bold;
}
.menu_active{
    font-weight:bold;
    border-bottom: 1px solid #565387;
}
.navbar a:hover{
    color:#3498db !important;
}

/* home section  */

#header {
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}

#header h1{
    margin:0 0 10px 0;
    font-size:48px;
    font-weight: 700;
    text-transform: capitalize;
    color: black;
}
#header .brand-name{
    color: #3498db;
}

#header h2{
    color: #484848;
    font-size: 24px;
    text-transform: capitalize;
}
#header .btn-get-started{
    font-weight: 800;
    text-decoration: none;
    font-size: 18px;
    letter-spacing: 1px;
    border: 2px solid #3498db;
    display:inline-block;
    border-radius: 25px;
    color: #3498db;
    padding:10px 30px;
    transition:0.5s;
    text-transform: capitalize;
}
#header .btn-get-started:hover{
    background-color: #3498db;
    color: white;
}
#header img{
    text-align: right;
}
#header .animated{
    animation:moving 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes moving {
    0%{
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }
    100%{
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
}
@keyframes moving {
    0%{
        -webkit-transform: translateY(15px);
                transform: translateY(15px);
    }
    100%{
        -webkit-transform: translateY(-15px);
                transform: translateY(-15px);
    }
}

.contact_div{
    height:73vh;
}
/* to make responsive */

@media (max-width:991px){
    #header{
        height:100vh;
        text-align: center;
    }
    #header .header-img{
        text-align: center;
    }
    #header .contentAdjust{
        display:grid;
        place-items: center; 
    }
    #header h1{
        font-size: 33px;
        text-align: center;
    }
    #header h2{
        font-size:22px;
        margin-bottom: 30px;
    }
    #header .header-img img{
        width:50%;
    }
    .navbar-brand{
        font-size: 1.5rem !important;
        color:#3498db !important;
        font-weight: bold;
    }
}
@media (max-width:768px){
    #header{
        margin-top:20px;
    }
    #header h1{
        font-size: 28px;
    }
    #header h2{
        font-size:18px;
        margin-bottom: 30px;
    }
    #header .header-img img{
        width:70%;
    }
}



